<template>
  <BaseLayout nonavbar>

    <van-sticky :offset-top="0">
      <van-row class="toolbar" gutter="0" justify="space-around" align="center">
        <van-col span="2">
          <van-icon name="arrow-left" color="#000" size="large" @click="$router.back()"></van-icon>
        </van-col>
        <van-col span="17">
          <van-search v-model="state.keyword" placeholder="请输入搜索关键词" background="transparent" input-align="left" @search="onSearch" />
        </van-col>
        <van-col span="2">
          <van-icon :name="showGrid ? 'bars' : 'apps-o'" size="large" @click="toggleGrid"></van-icon>
        </van-col>
        <van-col span="2"> 
          <van-icon name="cart-o" size="large" :class="state.animate" :badge="profile && profile.cartNumber ? profile.cartNumber : ''" @click="$router.push('cart')"></van-icon>
        </van-col>
        <van-col span="1"></van-col>
      </van-row>
      <van-dropdown-menu>
        <van-dropdown-item v-model="state.order" :options="state.options" @change="refresh" />
        <van-dropdown-item title="筛选">
          <van-cell center title="价格区间">
            <input type="number" v-model="state.min" :style="{width: '60px'}"/>
              ~
            <input type="number" v-model="state.max" :style="{width: '60px'}"/>
          </van-cell>
          <van-cell title="选择品牌">
            <template #extra>
              <van-checkbox @click="state.brand = [];refresh()">不指定品牌</van-checkbox>
            </template>
          </van-cell>
          <van-cell>
            <van-checkbox-group v-model="state.brand" direction="horizontal" checked-color="#ee0a24">
              <van-checkbox class="checkbox" v-for="(v,k) in state.brands" :key="k" :name="v">{{v}}</van-checkbox>
            </van-checkbox-group>
          </van-cell>
          <div style="padding: 5px 16px;">
            <van-button type="danger" block round @click="refresh">确认</van-button>
          </div>
        </van-dropdown-item>
      </van-dropdown-menu>
    </van-sticky>

    <van-pull-refresh v-model="state.refreshing" loading-text=" " @refresh="refresh">
      <van-list class="list" v-model:loading="state.loading" :finished="state.finished" loading-text="正在请求数据 ..." finished-text="没有更多了" :immediate-check="false" @load="query">
        <mall-item :grid="showGrid" :item="item" v-for="(item, index) in state.list" :key="index" @add="addCart"></mall-item>
        <van-empty v-show="state.list.length == 0" :style="{width:'100%'}"></van-empty>
      </van-list>
    </van-pull-refresh>

  </BaseLayout>
</template>

<style scoped>
.toolbar {
  background: #FFF url('~@/assets/bg_navbar.jpg') repeat left center;
  text-align:center;
}
.checkbox {
  margin-top: 8px;
  padding-right: 8px;
}
.list {
  display: flex;
  flex-flow: row wrap;
  align-content: flex-start;
  justify-content: space-evenly;
  min-height: 500px;
}
</style>

<script>
import {reactive} from 'vue'
import {mapState, mapGetters, mapMutations} from 'vuex'
import BaseLayout from '@/components/BaseLayout.vue'
import MallItem from '@/components/MallItem.vue'

export default {
  components: {BaseLayout, MallItem},
  name: "List",
  props: {
    brand: {
      type: String,
      default: "",
    },
    keyword: {
      type: String,
      default: "iphone",
    },
  },

  data() {
    const state = reactive({
      // 查询
      keyword: "",
      order: 0,
      options: [
        { text: '默认排序', value: 0 },
        { text: '销量', value: 1 },
        { text: '价格由低到高', value: 2 },
        { text: '价格由高到低', value: 3 },
      ],
      min: "",
      max: "",
      brand: [],
      page: 1,

      // 列表
      refreshing: false,
      loading: false,
      finished: false,
      list: [],
      brands: [],

      // 购物车动画
      animate: "",
    });

    return {state};
  },

  created(){
    
  },

  mounted() {
    if (!_.isEmpty(this.keyword))
      this.state.keyword = this.keyword;
    if (!_.isEmpty(this.brand))
      this.state.brand = this.brand.split(",");
    this.refresh();
  },  
  
  beforeRouteLeave(to, from, next){
    this.setScrollTop(document.getElementById("container").scrollTop);
    next();
  },

  computed: {
    ...mapState(["profile", "showGrid"]),
  },

  methods: {
    refresh() {
      this.state.loading = true;
      this.state.finished = false;
      this.state.page = 1;
      this.state.list = [];
      this.state.brands = [];
      this.query();
    },

    query() {
      this.$http.get("/jdmall/search?keyword=" + this.state.keyword + "&brand=" + this.state.brand.join(",") + "&order=" + this.state.order + "&max=" + this.state.max + "&min=" + this.state.min + "&page=" + this.state.page).then((data) => {
        this.state.refreshing = false;
        if (data.total == 0 || data.page == 0)
          this.state.finished = true;
        this.state.loading = false;
        this.state.page = data.page + 1;

        this.state.list = this.state.list.concat(data.list);

        this.state.brands = _.reduce(data.brand, function(result, v, k) {
          _.findIndex(result, v["name"]) == -1 && result.push(v["name"]);
          return result;
        }, []);
      });
    },

    onSearch() {
      this.$router.push("?keyword=" + this.state.keyword);
      this.refresh();
    },
    
    addCart(obj){
      this.$http.post("/mall/cartsave?spu=" + obj.id + "&sku=" + obj.id + "&num=1&status=1&channel=1").then((data) => {
        this.cartNumberAdd(1);
        this.state.animate = "animate__animated animate__shakeY";
        setTimeout(() => {
          this.state.animate = "";
        }, 1000);
      });
    },

    ...mapMutations(["togglePopup","toggleTabbar","toggleGrid","setScrollTop","cartNumberAdd"]),
  }
}
</script>
